import React from "react";
import Modal from "react-modal";
import Form from "muicss/lib/react/form";
import Button from "muicss/lib/react/button";
import Slider from "rc-slider";
import "rc-slider/assets/index.css";

class FilterModal extends React.Component {
  render() {
    const customStyles = {
      content: {
        top: 100,
        margin: "auto",
      },
    };
    const fourMarks = {
      0: "6 M",
      30: <strong>1 Y</strong>,
      65: "1.5 Y",
      100: "2 Y",
    };
    const sixMarks = {
      0: <strong>60 D</strong>,
      20: "90 D",
      40: "6 M",
      60: "1 Y",
      80: "1.5 Y",
      100: "2 Y",
    };
    const threeMarks = {
      0: <strong>60 D</strong>,
      50: "90 D",
      100: "6 M",
    };
    return (
      <Modal
        className="filter-modal"
        isOpen={this.props.isOpen}
        style={customStyles}
        ariaHideApp={false}
        contentLabel="Modal"
        shouldCloseOnOverlayClick={true}
      >
        <Form className="modal-form">
          <div className="modal-header">
            <i className="material-icons filter-icon">filter_list</i>
            <p>Filter</p>
            <i
              className="material-icons close-icon"
              onClick={this.props.closeModal}
            >
              close
            </i>
          </div>
          <div>
            <p>PW Properties</p>
            <div className="rkmd-checkbox checkbox-ripple">
              <label className="input-checkbox checkbox-lightBlue">
                <input
                  type="checkbox"
                  id="checkbox-1"
                  checked={this.props.pwChecked}
                  onChange={this.props.pwCheckboxChange}
                />
                <span className="checkbox"></span>
                <span className="ripple"></span>
              </label>
              <p htmlFor="checkbox-1" className="label"></p>
            </div>
          </div>
          <div>
            <p>IP Properties</p>
            <div className="rkmd-checkbox checkbox-ripple">
              <label className="input-checkbox checkbox-lightBlue">
                <input
                  type="checkbox"
                  id="checkbox-1"
                  checked={this.props.ipChecked}
                  onChange={this.props.ipCheckboxChange}
                />
                <span className="checkbox"></span>
                <span className="ripple"></span>
              </label>
              <p htmlFor="checkbox-1" className="label"></p>
            </div>
          </div>
          <div>
            <p>CL Properties</p>
            <div className="rkmd-checkbox checkbox-ripple">
              <label className="input-checkbox checkbox-lightBlue">
                <input
                  type="checkbox"
                  id="checkbox-1"
                  checked={this.props.clChecked}
                  onChange={this.props.clCheckboxChange}
                />
                <span className="checkbox"></span>
                <span className="ripple"></span>
              </label>
              <p htmlFor="checkbox-1" className="label"></p>
            </div>
            <div
              className={this.props.clChecked ? "slider-orange" : "slider-gray"}
            >
              <Slider
                marks={fourMarks}
                step={null}
                disabled={!this.props.clChecked}
                onChange={this.props.clSliderChange}
                value={this.props.clSlider}
              />
            </div>
          </div>
          <div>
            <p>Sales Comps</p>
            <div className="rkmd-checkbox checkbox-ripple">
              <label className="input-checkbox checkbox-lightBlue">
                <input
                  type="checkbox"
                  id="checkbox-1"
                  checked={this.props.salesChecked}
                  onChange={this.props.salesCheckboxChange}
                />
                <span className="checkbox"></span>
                <span className="ripple"></span>
              </label>
              <p htmlFor="checkbox-1" className="label"></p>
            </div>
            <div
              className={
                this.props.salesChecked ? "slider-orange" : "slider-gray"
              }
            >
              <Slider
                marks={fourMarks}
                step={null}
                disabled={!this.props.salesChecked}
                onChange={this.props.salesSliderChange}
                value={this.props.salesSlider}
              />
            </div>
          </div>
          <div>
            <p>Finance Comps (non {/* GPARENCY */})</p>
            <div className="rkmd-checkbox checkbox-ripple">
              <label className="input-checkbox checkbox-lightBlue">
                <input
                  type="checkbox"
                  id="checkbox-1"
                  checked={this.props.financeChecked}
                  onChange={this.props.financeCheckboxChange}
                />
                <span className="checkbox"></span>
                <span className="ripple"></span>
              </label>
              <p htmlFor="checkbox-1" className="label"></p>
            </div>
            <div
              className={
                this.props.financeChecked ? "slider-orange" : "slider-gray"
              }
            >
              <Slider
                marks={sixMarks}
                step={null}
                disabled={!this.props.financeChecked}
                onChange={this.props.financeSliderChange}
                value={this.props.financeSlider}
              />
            </div>
          </div>
          <div>
            <p>Recent Inactive PW/IP</p>
            <div className="rkmd-checkbox checkbox-ripple">
              <label className="input-checkbox checkbox-lightBlue">
                <input
                  type="checkbox"
                  id="checkbox-1"
                  checked={this.props.recentChecked}
                  onChange={this.props.recentCheckboxChange}
                />
                <span className="checkbox"></span>
                <span className="ripple"></span>
              </label>
              <p htmlFor="checkbox-1" className="label"></p>
            </div>
            <div
              className={
                this.props.recentChecked ? "slider-orange" : "slider-gray"
              }
            >
              <Slider
                marks={threeMarks}
                step={null}
                disabled={!this.props.recentChecked}
                onChange={this.props.recentSliderChange}
                value={this.props.recentSlider}
              />
            </div>
          </div>
          <div className="filter-btn">
            <Button variant="flat" onClick={this.props.submitFilter}>
              Filter
            </Button>
          </div>
        </Form>
      </Modal>
    );
  }
}

export default FilterModal;
