import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as qtsActions from "../../actions/qtsActions";
import QTSMap from "./QTSMap";
import Modal from "react-modal";
import Button from "muicss/lib/react/button";
import Expire from "../common/Expire";
import { Navigation } from "../../lib";
import getParameterByName from "../common/getParameterByName";
import encodeQueryData from "../common/encodeQueryData";
import AskQtsSearchResults from "./AskQtsSearchResults";

const google = window.google;

class QTSMapContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filterModalOpen: false,
      clSlider: sessionStorage.getItem("qts_range_a")
        ? parseInt(sessionStorage.getItem("qts_range_a"), 10)
        : 0,
      salesSlider: sessionStorage.getItem("qts_range_b")
        ? parseInt(sessionStorage.getItem("qts_range_b"), 10)
        : 0,
      financeSlider: sessionStorage.getItem("qts_range_c")
        ? parseInt(sessionStorage.getItem("qts_range_c"), 10)
        : 40,
      recentSlider: sessionStorage.getItem("qts_range_d")
        ? parseInt(sessionStorage.getItem("qts_range_d"), 10)
        : 0,
      ipChecked: sessionStorage.getItem("qts_ipChecked")
        ? sessionStorage.getItem("qts_ipChecked")
        : true,
      pwChecked: sessionStorage.getItem("qts_pwChecked")
        ? sessionStorage.getItem("qts_pwChecked")
        : true,
      clChecked: sessionStorage.getItem("qts_clChecked")
        ? sessionStorage.getItem("qts_clChecked")
        : true,
      salesChecked: sessionStorage.getItem("qts_salesChecked")
        ? sessionStorage.getItem("qts_salesChecked")
        : true,
      financeChecked: sessionStorage.getItem("qts_financeChecked")
        ? sessionStorage.getItem("qts_financeChecked")
        : true,
      recentChecked: sessionStorage.getItem("qts_show_inactive") === "true",
      mapData: {},
      pathname: window.location.href,
    };
  }

  pwCheckboxChange() {
    this.setState({ pwChecked: !this.state.pwChecked });
  }

  ipCheckboxChange() {
    this.setState({ ipChecked: !this.state.ipChecked });
  }

  clCheckboxChange() {
    this.setState({ clChecked: !this.state.clChecked });
  }

  salesCheckboxChange() {
    this.setState({ salesChecked: !this.state.salesChecked });
  }

  financeCheckboxChange() {
    this.setState({ financeChecked: !this.state.financeChecked });
  }

  recentCheckboxChange() {
    this.setState({ recentChecked: !this.state.recentChecked });
  }

  recentSliderChange(e) {
    this.setState({ recentSlider: e });
  }

  financeSliderChange(e) {
    this.setState({ financeSlider: e });
  }

  salesSliderChange(e) {
    this.setState({ salesSlider: e });
  }

  clSliderChange(e) {
    this.setState({ clSlider: e });
  }

  componentDidMount() {
    if (!sessionStorage.getItem("qts_address")) {
      sessionStorage.setItem(
        "qts_address",
        getParameterByName("address") || ""
      );
    }
    //work on this address further since usually its a JSON address component
    if (!sessionStorage.getItem("qts_bank")) {
      sessionStorage.setItem("qts_bank", getParameterByName("bank") || "");
    }
    if (!sessionStorage.getItem("qts_radius")) {
      sessionStorage.setItem("qts_radius", getParameterByName("radius") || "");
    }
    if (!sessionStorage.getItem("qts_propertyTypes")) {
      sessionStorage.setItem(
        "qts_propertyTypes",
        getParameterByName("property_types") || ""
      );
    }
    this.loadQTSMap();
  }

  loadQTSMap() {
    let range_a = ``;
    let cls = parseInt(this.state.clSlider, 10);
    range_a = cls === 0 ? 6 : cls === 30 ? 12 : cls === 65 ? 18 : 24;

    let range_b = ``;
    let sls = parseInt(this.state.salesSlider, 10);
    range_b = sls === 0 ? 6 : sls === 30 ? 12 : sls === 65 ? 18 : 24;

    let range_c = ``;
    let fs = parseInt(this.state.financeSlider, 10);
    range_c =
      fs === 0
        ? 2
        : fs === 20
        ? 3
        : fs === 40
        ? 6
        : fs === 60
        ? 12
        : fs === 80
        ? 18
        : 24;

    let range_d = ``;
    let rs = parseInt(this.state.recentSlider, 10);
    range_d = rs === 0 ? 2 : rs === 50 ? 3 : 6;

    this.props.actions.loadQTSMap(
      getParameterByName("latitude"),
      getParameterByName("longitude"),
      getParameterByName("city"),
      getParameterByName("state"),
      getParameterByName("property_types")
        ? decodeURIComponent(getParameterByName("property_types"))
            .split(",")
            .map((p) => parseInt(p, 10))
        : [],
      getParameterByName("radius") === "city"
        ? ""
        : getParameterByName("radius"),
      getParameterByName("date_range"),
      range_a,
      range_b,
      range_c,
      range_d,
      this.state.recentChecked ? 1 : "",
      this.state.ipChecked,
      this.state.pwChecked,
      this.state.clChecked,
      this.state.salesChecked,
      this.state.financeChecked,
      getParameterByName("bank"),
      getParameterByName("in_nyc")
        ? parseInt(getParameterByName("in_nyc"), 10)
        : null
    );
  }

  UNSAFE_componentWillReceiveProps() {
    if (this.state.pathname !== window.location.href) {
      this.loadQTSMap();
      this.setState({ pathname: window.location.href });
    }
  }

  submitFilterQTSMap() {
    sessionStorage.setItem("qts_range_a", this.state.clSlider);
    sessionStorage.setItem("qts_range_b", this.state.salesSlider);
    sessionStorage.setItem("qts_range_c", this.state.financeSlider);
    sessionStorage.setItem("qts_range_d", this.state.recentSlider);
    sessionStorage.setItem("qts_show_inactive", this.state.recentChecked);
    sessionStorage.setItem("qts_ipChecked", this.state.ipChecked);
    sessionStorage.setItem("qts_pwChecked", this.state.pwChecked);
    sessionStorage.setItem("qts_clChecked", this.state.clChecked);
    sessionStorage.setItem("qts_salesChecked", this.state.salesChecked);
    sessionStorage.setItem("qts_financeChecked", this.state.financeChecked);

    this.loadQTSMap();
  }

  navigateToStreetView(latitude, longitude, address) {
    var sv = new google.maps.StreetViewService();
    sv.getPanorama(
      {
        location: {
          lat: parseFloat(latitude),
          lng: parseFloat(longitude),
        },
        radius: 50,
      },
      (data, status) => {
        if (status === "OK") {
          Navigation.go(
            `/comp-sv?${encodeQueryData({
              latitude: latitude,
              longitude: longitude,
              address: address || data.location.description,
            })}`
          );
        } else {
          this.props.actions.updateErrorToast(
            "No street view available for this property"
          );
        }
      }
    );
  }

  render() {
    let toast = null;
    if (this.props.qts.toast.show) {
      toast = (
        <Expire closeToast={this.props.actions.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.qts.toast.message}</p>
            <Button variant="flat" onClick={this.props.actions.closeToast}>
              {this.props.qts.toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }
    return (
      <AskQtsSearchResults>
        {toast}
        {this.props.qts.loadingMap ? (
          <Modal
            className="loading-modal"
            isOpen={this.props.qts.loadingMap}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div
              className={
                this.props.qts.loadingMap ? "centered-spinner" : "hidden"
              }
            >
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        ) : (
          <QTSMap
            mapData={this.props.qts.mapData}
            center={this.props.qts.mapCenter}
            radius={this.props.match.params.radius}
            clSlider={this.state.clSlider}
            salesSlider={this.state.salesSlider}
            financeSlider={this.state.financeSlider}
            recentSlider={this.state.recentSlider}
            ipChecked={this.state.ipChecked}
            pwChecked={this.state.pwChecked}
            clChecked={this.state.clChecked}
            salesChecked={this.state.salesChecked}
            financeChecked={this.state.financeChecked}
            recentChecked={this.state.recentChecked}
            navigateToStreetView={this.navigateToStreetView.bind(this)}
            submitFilter={this.submitFilterQTSMap.bind(this)}
            pwCheckboxChange={this.pwCheckboxChange.bind(this)}
            ipCheckboxChange={this.ipCheckboxChange.bind(this)}
            clCheckboxChange={this.clCheckboxChange.bind(this)}
            salesCheckboxChange={this.salesCheckboxChange.bind(this)}
            financeCheckboxChange={this.financeCheckboxChange.bind(this)}
            recentCheckboxChange={this.recentCheckboxChange.bind(this)}
            recentSliderChange={this.recentSliderChange.bind(this)}
            financeSliderChange={this.financeSliderChange.bind(this)}
            salesSliderChange={this.salesSliderChange.bind(this)}
            clSliderChange={this.clSliderChange.bind(this)}
          />
        )}
      </AskQtsSearchResults>
    );
  }
}

function mapStateToProps(state) {
  return {
    comps: state.comps,
    qts: state.QTS,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(qtsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QTSMapContainer);
