import React from "react";
import { Navigation } from "../../lib";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Select from "muicss/lib/react/select";
import Input from "muicss/lib/react/input";
import Option from "muicss/lib/react/option";
import Button from "muicss/lib/react/button";
import MultiSelect from "./MultiSelect";
import getParameterByName from "../common/getParameterByName";
import encodeQueryData from "../common/encodeQueryData";

const google = window.google;

class SharedSearchForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      initialAddress: "",
      addressChanged: false,
      radius: "3",
      property_types: [],
      bank: "",
      bankName: "",
      localPropertyTypes: [],
      propertyToggleDropdown: false,
      autocompleteAddress: "",
    };
  }

  componentDidMount() {
    this.loadGoogleAutocomplete();
    this.setState({
      localPropertyTypes: [
        { list_type_id: 0, value: "All" },
        ...this.props.propertyTypesList,
      ],
      initialAddress: sessionStorage.getItem("qts_address")
        ? JSON.parse(sessionStorage.getItem("qts_address")).address
        : getParameterByName("address")
        ? getParameterByName("address")
        : "",
      addressChanged: false,
      radius: sessionStorage.getItem("qts_radius")
        ? sessionStorage.getItem("qts_radius")
        : getParameterByName("radius")
        ? getParameterByName("radius")
        : "3",
      property_types: sessionStorage.getItem("qts_propertyTypes")
        ? sessionStorage.getItem("qts_propertyTypes").split(",")
        : getParameterByName("property_types")
        ? decodeURIComponent(getParameterByName("property_types")).split(",")
        : [],
      bank: sessionStorage.getItem("qts_bank")
        ? sessionStorage.getItem("qts_bank")
        : getParameterByName("bank")
        ? getParameterByName("bank")
        : "",
    });
  }

  loadGoogleAutocomplete() {
    let options = {
      componentRestrictions: { country: "us" },
    };
    var searchbox = new google.maps.places.Autocomplete(
      this.refs.autocomplete,
      options
    );
    searchbox.addListener(
      "place_changed",
      function () {
        this.refs.autocomplete.blur();
        this.place = searchbox.getPlace();
        if (this.place.geometry) {
          try {
            this.setState({
              autocompleteAddress: this.place,
              initialAddress: this.place.formatted_address,
            });
          } catch (err) {
            alert(err);
          }
        } else {
          alert("there was an error with google autocomplete");
        }
      }.bind(this)
    );
  }

  componentDidUpdate() {
    this.loadGoogleAutocomplete();
  }

  propertyToggleDropdown() {
    let show = !this.state.propertyToggleDropdown;
    this.setState({ propertyToggleDropdown: show }, function () {
      if (show) {
        if (this.state.lenderToggleDropdown) {
          this.setState({ lenderToggleDropdown: false });
        }
      }
    });
  }

  propertyTypeCheckboxChange(selectedOptions) {
    this.setState({ property_types: selectedOptions });
  }

  getMultiSelectTextValue() {
    let textValue = "";
    this.state.property_types &&
      this.state.property_types.map((value, index) => {
        let type = this.state.localPropertyTypes.find(
          (t) => t.list_type_id === value
        );
        if (type) {
          if (index >= 1) {
            textValue += ", ";
          }
          textValue += type.value;
        }
        return textValue;
      });
    return textValue.length > 0 ? textValue : "Property Types (optional)";
  }

  onChangeAddress(e) {
    if (e.keyCode === 13) {
      e.preventDefault();
    }
    this.setState({
      addressChanged: true,
      initialAddress: e.target.value,
    });
  }

  onChangeRadius(e) {
    this.setState(
      {
        radius: e.target.value,
      },
      sessionStorage.setItem("qts_radius", this.state.radius)
    );
  }

  onChangeBankName(e) {
    this.setState({
      bankName: e.target.value,
    });
    sessionStorage.setItem("qts_bank", this.state.bankName);
  }

  submitSearch(e) {
    e.preventDefault();
    this.setState({ propertyToggleDropdown: false });
    var localAddress = "";
    try {
      localAddress = JSON.parse(sessionStorage.getItem("qts_address"));
    } catch (err) {}
    var latitude = this.state.autocompleteAddress.geometry
      ? this.state.autocompleteAddress.geometry.location.lat()
      : localAddress.latitude;
    var longitude = this.state.autocompleteAddress.geometry
      ? this.state.autocompleteAddress.geometry.location.lng()
      : localAddress.longitude;
    var stateObject = this.state.autocompleteAddress.address_components
      ? this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("administrative_area_level_1")
        )
      : localAddress.state;
    var cityObject = this.state.autocompleteAddress.address_components
      ? this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("locality")
        ) ||
        this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("sublocality")
        )
      : localAddress.city;
    var inNYCObject = this.state.autocompleteAddress.address_components
      ? this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("administrative_area_level_2")
        )
      : "";
    if (!stateObject) {
      alert("You must search by a precise location");
    } else {
      var state = stateObject.short_name;
      var city = cityObject.long_name;
      var inNYC = inNYCObject && inNYCObject.long_name;
      var addressObject = {
        address: this.state.autocompleteAddress
          ? this.state.autocompleteAddress.formatted_address
          : this.state.initialAddress,
        longitude: longitude,
        latitude: latitude,
        state: stateObject,
        city: cityObject,
      };
      sessionStorage.setItem("qts_address", JSON.stringify(addressObject));
    }

    sessionStorage.setItem("qts_radius", this.state.radius);
    sessionStorage.setItem(
      "qts_propertyTypes",
      this.state.property_types.length
        ? this.state.property_types.join(",")
        : ""
    );
    sessionStorage.setItem("qts_bank", this.state.bankName);

    inNYC === "New York County" ||
    inNYC === "Bronx County" ||
    inNYC === "Kings County" ||
    inNYC === "Queens County" ||
    inNYC === "Richmond County"
      ? sessionStorage.setItem("qts_in_nyc", 1)
      : sessionStorage.setItem("qts_in_nyc", 0);

    var data = {
      latitude: latitude,
      longitude: longitude,
      state: state,
      city: this.state.radius === "city" ? city : "",
      property_types: this.state.property_types.length
        ? encodeURIComponent(this.state.property_types)
        : "",
      radius: this.state.radius === "city" ? "" : this.state.radius,
      date_range: getParameterByName("date_range"), //not in local
      min_loan: getParameterByName("min_loan")
        ? getParameterByName("min_loan").replace(/\D/g, "")
        : "",
      max_loan: getParameterByName("max_loan")
        ? getParameterByName("max_loan").replace(/\D/g, "")
        : "",
      bank: this.state.bankName,
      banker: getParameterByName("banker"),
      lender_type: getParameterByName("lender_type"),
      note_search: getParameterByName("note_search"),
      search_type: getParameterByName("search_type"),
      address: this.state.initialAddress,
      in_nyc:
        inNYC === "New York County" ||
        inNYC === "Bronx County" ||
        inNYC === "Kings County" ||
        inNYC === "Queens County" ||
        inNYC === "Richmond County"
          ? 1
          : 0,
    };
    Object.keys(data).forEach(
      (key) => (data[key] === null || data[key] === "") && delete data[key]
    );
    Navigation.go(`/qts-map?${encodeQueryData(data)}`);
  }

  render() {
    return (
      <div
        className={
          this.props.dropdownShowing
            ? "qts-search-dropdown-box mui--z1"
            : "hidden-opacity"
        }
      >
        <div className="qts-search-dropdown-box-content">
          <div className="dropdown-box-header">
            <i className="material-icons search">search</i>
            <p>Search</p>
            <i
              className="material-icons close"
              onClick={this.props.toggleDropdown}
            >
              close
            </i>
          </div>
          <Row>
            <Col md="12" className="address-container">
              <input
                className="input"
                type="text"
                ref="autocomplete"
                value={this.state.initialAddress}
                onChange={this.onChangeAddress.bind(this)}
                placeholder="Address"
                style={{ color: "#000" }}
              />
            </Col>
            <Col
              md="12"
              className="property-container"
              onClick={this.propertyToggleDropdown.bind(this)}
            >
              <p className="offset-column property-type-header">
                {this.getMultiSelectTextValue()}
              </p>
              <i className="material-icons property-arrow">arrow_drop_down</i>
              <MultiSelect
                options={this.state.localPropertyTypes}
                selectedOptions={this.state.property_types}
                showDropdown={this.state.propertyToggleDropdown}
                checkboxChange={this.propertyTypeCheckboxChange.bind(this)}
              />
              {/*<i onClick={this.toggleDropdown.bind(this)}*/}
              {/*className="material-icons property-arrow">arrow_drop_down</i>*/}
              {/*<div className={this.state.toggleDropdown ? "qts-dropdown-box mui--z1" : "hidden"}>*/}
              {/*{propertyTypes}*/}
              {/*</div>*/}
            </Col>

            <Col md="12" className="radius-container">
              <Select
                value={this.state.radius.toString()}
                onChange={this.onChangeRadius.bind(this)}
              >
                <Option
                  className="comps-search-disabled"
                  value=""
                  label="Radius"
                />
                <Option value="0.25" label="1/4 Mile" />
                <Option value="0.5" label="1/2 Mile" />
                <Option value="1" label="1 Mile" />
                <Option value="3" label="3 Miles" />
                <Option value="5" label="5 Miles" />
                <Option value="city" label="Entire City" />
                <Option value="10" label="10 Miles" />
                <Option value="25" label="25 Miles" />
                <Option value="50" label="50 Miles" />
                <Option value="60" label="60 Miles" />
              </Select>
            </Col>
            <Col md="12" className="bank-container">
              <Input
                type="text"
                onChange={this.onChangeBankName.bind(this)}
                value={this.state.bankName}
                hint="Bank Name"
              />
            </Col>
          </Row>
          <Row className="floating-row">
            <Col
              md="12"
              className="reset-filters-container"
              onClick={this.props.toggleDropdown}
            >
              <Button
                className="search-drpdwn-search-btn"
                onClick={this.submitSearch.bind(this)}
              >
                Search
              </Button>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}

export default SharedSearchForm;
