import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Link, NavLink } from "react-router-dom";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as qtsActions from "../../actions/qtsActions";
import Row from "muicss/lib/react/row";
import SharedSearchForm from "./SharedSearchForm";
import Modal from "react-modal";

class AskQTSSearchResults extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dropdownShowing: false,
    };
    this.toggleDropdown = this.toggleDropdown.bind(this);
  }

  async UNSAFE_componentWillMount() {
    await this.props.qtsActions.getPropertyTypes();
  }

  toggleDropdown() {
    this.setState({ dropdownShowing: !this.state.dropdownShowing });
  }

  render() {
    return (
      <div className="qts-page">
        {this.props.qts.propertyTypeList.length > 0 ? (
          <div>
            <Row className="sub-header">
              <Link className="back-arrow" to={`/qts-search`}>
                <img src="/images/icons/arrow-left.png" alt="arrow-left" />
              </Link>
              <ul className="qts-nav">
                <li className="qts-nav-list-item list-item2">
                  <NavLink
                    to={{
                      pathname: `/qts-banks`,
                      search: window.location.search,
                    }}
                    className="qts-nav-item"
                    activeClassName="header-active-link"
                  >
                    POTENTIAL LENDERS
                  </NavLink>
                </li>
                <li className="qts-nav-list-item list-item2">
                  <NavLink
                    to={{
                      pathname: `/qts-rates`,
                      search: window.location.search,
                    }}
                    className="qts-nav-item"
                    activeClassName="header-active-link"
                  >
                    Rates
                  </NavLink>
                </li>
                <li className="qts-nav-list-item list-item2">
                  <NavLink
                    to={{
                      pathname: `/qts-map`,
                      search: window.location.search,
                    }}
                    className="qts-nav-item"
                    activeClassName="header-active-link"
                  >
                    Map
                  </NavLink>
                </li>
                <li className="qts-nav-list-item list-item3">
                  <NavLink
                    to={{
                      pathname: `/qts-brokers`,
                      search: window.location.search,
                    }}
                    className="qts-nav-item"
                    activeClassName="header-active-link"
                  >
                    Active Brokers
                  </NavLink>
                </li>
              </ul>
              <div style={{ position: "absolute", top: "20px", right: "24px" }}>
                <i
                  className="material-icons search-icon"
                  onClick={this.toggleDropdown}
                  style={{ color: "#fff", cursor: "pointer" }}
                >
                  search
                </i>
              </div>
            </Row>
            <div className="qts-container">{this.props.children}</div>
            <SharedSearchForm
              dropdownShowing={this.state.dropdownShowing}
              toggleDropdown={this.toggleDropdown}
              propertyTypesList={this.props.qts.propertyTypeList}
            />
          </div>
        ) : (
          <Modal
            className="loading-modal"
            ariaHideApp={false}
            contentLabel="modal"
            isOpen={true}
            shouldCloseOnOverlayClick={false}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    title: state.navTitle,
    qts: state.QTS,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    qtsActions: bindActionCreators(qtsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AskQTSSearchResults);
